.menu {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  z-index: 2;
}

.menu__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}

.menu__item {
  box-sizing: border-box;
  padding-left: 76px;
  width: 100%;
  height: 79px;
  box-shadow: 0px 1px 0px #ECECEC, inset 0px 1px 0px #ECECEC;
}

.menu__item--logo {
  padding: 0;
}

.menu__item--search {
  background-image: url("../../img/search.svg");
  background-repeat: no-repeat;
  background-position: left 32px center;
}

.menu__item--cart {
  background-image: url("../../img/cart.svg");
  background-repeat: no-repeat;
  background-position: left 32px center;
}

.menu__item--hidden {
  display: none;
}

.menu__link {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: inherit;
}

.menu__link--logo {
  justify-content: center;
}

@media (min-width: 768px) {
  .menu__list {
    display: grid;
    grid-template-columns: 218px 153px 82px 315px;
    grid-template-rows: 88px 67px;
  }

  .menu__item {
    width: auto;
    height: 100%;
    max-height: 100%;
    padding: 0;
    box-shadow: none;
  }

  .menu__item--logo {
    order: 1;
    grid-column: 1/3;
    padding-left: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-right: 1px solid rgba(0, 0, 0, 0.2);
  }
  .menu__item--logo .menu__link {
    justify-content: flex-start;
  }

  .menu__item--search {
    order: 2;
    grid-column: 3/4;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  .menu__item--search .menu__link {
    font-size: 0;
  }

  .menu__item--cart {
    order: 3;
    grid-column: 4/6;
    padding-right: 50px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-left: 1px solid rgba(0, 0, 0, 0.2);
  }
  .menu__item--cart .menu__link {
    justify-content: flex-end;
  }

  .menu__item--catalog {
    order: 4;
    padding-left: 50px;
    background-color: transparent;
  }
  .menu__item--catalog .menu__link {
    font-size: 15px;
    line-height: 24px;
  }

  .menu__item--form {
    order: 5;
    background-color: transparent;
  }
  .menu__item--form .menu__link {
    font-size: 15px;
    line-height: 24px;
  }

  .menu__item--delivery {
    order: 6;
    grid-column: -1/-3;
    padding-right: 50px;
    display: block;
    background-color: transparent;
  }
  .menu__item--delivery .menu__link {
    justify-content: flex-end;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #444444;
  }
}
