  line-height: 18px;
  color: #000000;
  user-select: none;
  cursor: pointer;
}
.checkbox__label::before {
  content: "";
  box-sizing: border-box;
  width: 38px;
  height: 38px;
  border: 2px solid #444444;
  flex-shrink: 0;
  margin-right: 24px;
}
.checkbox__label::after {
  content: "";
  box-sizing: border-box;
  width: 22px;
